// MovieDetail TvDetail Css
import { motion } from "framer-motion";
import { styled } from "styled-components";

// Card         
export const Overlay = styled(motion.div)`
    position: fixed;
    top: 0;
    left: 0px;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: 0;
`;

// Card     
export const Info = styled(motion.div)`
    width: 50vw;
    height: 80vh;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    background-color: ${(props) => props.theme.black.lighter};
    border-radius: 20px;
    overflow: auto;

    -ms-overflow-style: none;
    scrollbar-width: none;
    &::-webkit-scrollbar {
        display: none;
    }
`;

//      
export const InfoCover = styled.div`
    width: 100%;
    height: 400px;
    background-size: cover;
    background-position: center center;
    display: grid;
    grid-template-columns: 220px auto;
`;

//         div
export const InfoPosterWrapper = styled.div`
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
`;

//       
export const InfoPoster = styled.img`
    width: 200px;
    height: 300px;
    margin-left: 20px;
`;

//         div
export const InfoTitleWrapper = styled.div``;

//       
export const InfoTitle = styled.h2`
    color: ${(props) => props.theme.white.lighter};
    font-size: 35px;
    margin-top: 50px;
    margin-left: 10px;
`;

//        
export const InfoOriginalTitle = styled.h4`
    font-weight: bold;
    font-size: 24px;
    margin-top: 10px;
    margin-left: 20px;
`;

//       
export const InfoTagline = styled.h6`
    color: ${(props) => props.theme.white.lighter};
    font-size: 18px;
    margin-top: 10px;
    margin-left: 20px;
    &::before {
        content: " - ";
        color: ${(props) => props.theme.white.darker};
    }
`;

//        
export const InfoString = styled.h6`
    color: ${(props) => props.theme.white.lighter};
    font-size: 14px;
    margin-top: 10px;
    margin-left: 25px;
    &::before {
        content: "";
        color: ${(props) => props.theme.white.darker};
    }
`;

//    div
export const InfoContainer = styled.div`
    padding: 20px;
    display: grid;
    grid-template-columns: 200px 2px auto;
`;

//          div
export const InfoLogo = styled.div`
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
`;

//         div
export const LogoContainer = styled.div`
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
`;

//       
export const Logo = styled.img`
    background-color: ${(props) => props.theme.white.lighter};
    width: 50px;
    height: 50px;
`;

//       
export const InfoOverView = styled.p`
    padding: 20px;
    color: ${(props) => props.theme.white.lighter};
`;

// Div   
export const HorizonBar = styled.div`
    background-color: ${(props) => props.theme.white.lighter};
    width: 100%;
    height: 100%;
`;
