// Card     
import { useQuery } from "react-query";
import { PathMatch, useMatch, useNavigate } from "react-router-dom";
import { getVideoDetail, IGetDetailMovieResult } from "../../api";
import { makeImagePath } from "../../utils";
import * as detail from "../../css/Detail";

/**
 * Movie List Card      
 * @param keyword Search  
 * @param videoId   ID
 * @returns   Div
 */
function MovieDetail({
    keyword,
    videoId,
}: {
    keyword?: string;
    videoId: string;
}) {
    //   
    const navigate = useNavigate();
    //         
    const onOverlayClicked = () => {
        if (keyword != undefined) {
            navigate(`/search?keyword=${keyword}`);
        } else {
            navigate(`/`);
        }
    };
    //    route  
    const detailMovieMatch: PathMatch<string> | null = useMatch(
        "/movies/:category/:Id"
    );
    //  ID    
    const { data, isLoading } = useQuery<IGetDetailMovieResult>(
        ["movie", "detail"],
        () => getVideoDetail("movie", videoId)
    );

    return (
        <>
            <detail.Overlay
                onClick={onOverlayClicked}
                animate={{ opacity: 1 }}
                exit={{ opacity: 0 }}
            />
            <detail.Info layoutId={detailMovieMatch?.params.category + videoId}>
                <detail.InfoCover
                    style={{
                        backgroundImage: `linear-gradient(to top,black, transparent), url(${makeImagePath(
                            String(data?.backdrop_path || ""),
                            ""
                        )})`,
                    }}
                >
                    <detail.InfoPosterWrapper>
                        <detail.InfoPoster
                            src={makeImagePath(
                                String(data?.poster_path || ""),
                                "w200"
                            )}
                        />
                    </detail.InfoPosterWrapper>
                    <detail.InfoTitleWrapper>
                        <detail.InfoTitle>{data?.title}</detail.InfoTitle>
                        <detail.InfoOriginalTitle>
                            {data?.original_title}
                        </detail.InfoOriginalTitle>
                        <detail.InfoTagline>
                            {data?.tagline || " "}
                        </detail.InfoTagline>
                        <detail.InfoString>
                            {" : " + data?.release_date}
                        </detail.InfoString>
                        <detail.InfoString>
                            {" : " +
                                data?.production_countries[0]?.name}
                        </detail.InfoString>
                        <detail.InfoString>
                            {" : " +
                                data?.genres?.map((props) => " " + props.name)}
                        </detail.InfoString>
                        <detail.InfoString>
                            {" : " + data?.vote_average?.toFixed(1)}
                        </detail.InfoString>

                        <detail.InfoString>
                            {" : " +
                                data?.spoken_languages[0]?.english_name}
                        </detail.InfoString>
                        <detail.InfoString>
                            {"  : " + data?.runtime + ""}
                        </detail.InfoString>
                    </detail.InfoTitleWrapper>
                </detail.InfoCover>
                <detail.InfoContainer>
                    <detail.InfoLogo>
                        {data?.production_companies?.map((props) => (
                            <detail.LogoContainer key={props.id}>
                                <detail.Logo
                                    key={props.id}
                                    src={makeImagePath(props.logo_path, "w200")}
                                    title={props.name}
                                />
                            </detail.LogoContainer>
                        ))}
                    </detail.InfoLogo>
                    <detail.HorizonBar />
                    <detail.InfoOverView>
                        {data?.overview || "  "}
                    </detail.InfoOverView>
                </detail.InfoContainer>
            </detail.Info>
        </>
    );
}

export default MovieDetail;
