// Tv Card   Slider
import { AnimatePresence } from "framer-motion";
import { useRef, useState } from "react";
import { IoIosArrowDropright, IoIosArrowDropleft } from "react-icons/io";
import { IGetTvResult } from "../../api";
import { useWindowDimensions } from "../../utils";
import * as Cards from "../../css/Cards";
import Card from "./Card";

/**
 * Card   Slider 
 * @param data API   
 * @param cardsName Cards   
 * @param tagName Cards  :  X
 * @param cut  cut   Card  
 * - 2    2   
 * - Banner  1  1 
 * @param top Cards y   : ex) -200px 
 * @returns   Div
 */
function TvCards({
    data,
    cardsName,
    tagName,
    cut,
    top,
}: {
    data: IGetTvResult;
    cardsName: "Popular" | "Top Rated" | "Airing Today";
    tagName: "Popular" | "TopRated" | "AiringToday";
    cut: number;
    top: string;
}) {
    // SliderWrapper  Slider  
    const [index, setIndex] = useState(0);
    // Slider    
    const [leaving, setLeaving] = useState(false);
    const toggleLeaving = () => setLeaving((prev) => !prev);
    // Slider      
    const isBack = useRef(false);
    //      
    const width = useWindowDimensions();
    // offset :    Card 
    let offset = 8;
    //  
    if (1870 <= window.innerWidth) {
        offset = 8;
    } else if (1650 <= window.innerWidth && window.innerWidth < 1870) {
        offset = 7;
    } else if (1425 <= window.innerWidth && window.innerWidth < 1650) {
        offset = 6;
    } else if (1200 <= window.innerWidth && window.innerWidth < 1425) {
        offset = 5;
    } else if (975 <= window.innerWidth && window.innerWidth < 1200) {
        offset = 4;
    } else if (752 <= window.innerWidth && window.innerWidth < 975) {
        offset = 3;
    } else {
        offset = 2;
    }
    //   
    const totalMovies = data.results.length - 1;
    //    / offset :  Slider 
    const maxIndex = Math.ceil(totalMovies / offset) - 1;
    // index 
    const increaseIndex = () => {
        if (data) {
            if (leaving) return;

            toggleLeaving();

            // index Slider    0 
            setIndex((prev) => (prev >= maxIndex ? 0 : prev + 1));
            isBack.current = false;
        }
    };
    const decreaseIndex = () => {
        if (data) {
            if (leaving) return;

            toggleLeaving();

            // index Slider    0 
            setIndex((prev) => (prev == 0 ? maxIndex : prev - 1));
            isBack.current = true;
        }
    };
    //     
    const sliderVariants = {
        hidden: ({ width }: { width: number }) => ({
            x: isBack.current ? -width : width,
        }),
        visible: {
            x: 0,
        },
        exit: ({ width }: { width: number }) => ({
            x: isBack.current ? width : -width,
        }),
    };

    return (
        <Cards.CardsWrapper style={{ top: top }}>
            <Cards.Cardsheader>
                <Cards.CardsTitle>{cardsName}</Cards.CardsTitle>
                <Cards.CardsArrowContainer>
                    <Cards.CardsArrow
                        variants={Cards.CardsArrowVariant}
                        whileHover="hover"
                        initial="normal"
                    >
                        <IoIosArrowDropleft onClick={decreaseIndex} />
                    </Cards.CardsArrow>
                    <Cards.Index>
                        {index + 1 + " / " + (maxIndex + 1)}
                    </Cards.Index>
                    <Cards.CardsArrow
                        variants={Cards.CardsArrowVariant}
                        whileHover="hover"
                        initial="normal"
                    >
                        <IoIosArrowDropright onClick={increaseIndex} />
                    </Cards.CardsArrow>
                </Cards.CardsArrowContainer>
            </Cards.Cardsheader>
            {/*       */}
            {/* onExitComplete :      */}
            <AnimatePresence initial={false} onExitComplete={toggleLeaving}>
                <Cards.Slider
                    key={index}
                    offset={offset}
                    variants={sliderVariants}
                    initial="hidden"
                    animate="visible"
                    exit="exit"
                    custom={{ width, isBack }}
                    transition={{ type: "tween", duration: 1 }}
                    style={{ translateX: "-50%" }}
                >
                    {/*      */}
                    {data?.results
                        .slice(cut)
                        .slice(
                            // 6  
                            offset * index,
                            offset * index + offset
                        )
                        .map((movie) => (
                            <Card
                                key={movie.id}
                                category={"tv"}
                                keyword={null}
                                tagName={tagName}
                                id={movie.id}
                                backdrop_path={movie.backdrop_path}
                                poster_path={movie.poster_path}
                                title={movie.name}
                            />
                        ))}
                </Cards.Slider>
            </AnimatePresence>
        </Cards.CardsWrapper>
    );
}

export default TvCards;
